body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.counter {
    font-size: 20px;
    background-color: #45cbbb;
    padding: 10px;
    margin-top: 10px;
    color: blue;
    font-weight: bold;
}

.header {
    background-color: green;
    padding: 20px 10px;
    color: whitesmoke;
    text-transform: uppercase;
}
.animation {
    height: 100px;
    position: relative;
    width: 300px;
    display: inline-block;
}

.animation__circle {
    position: absolute;
    top: 10px;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    -webkit-animation: move 1s linear infinite alternate, changeColor 1s linear infinite alternate;
            animation: move 1s linear infinite alternate, changeColor 1s linear infinite alternate;
}

.animation__circle::before,
.animation__circle::after {
    content: '';
    position: absolute;
    border-radius: 50%;
    width: 10px;
    height: 10px;
    background-color: white;
}

.animation__circle::before {
    top: 20px;
    left: 20px;
}

.animation__circle::after {
    top: 20px;
    right: 20px;
}

@-webkit-keyframes changeColor {
    from {
        background-color: blue;
    }   

    50% {
        background-color: red;
    }
  
    to {
        background-color: blue;
    }
}

@keyframes changeColor {
    from {
        background-color: blue;
    }   

    50% {
        background-color: red;
    }
  
    to {
        background-color: blue;
    }
}

@-webkit-keyframes move {
    from {
        left: 0;
    }
  
    to {
        left: calc(100% - 80px);
    }
}

@keyframes move {
    from {
        left: 0;
    }
  
    to {
        left: calc(100% - 80px);
    }
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

